import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/pages/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/rooms',
    name: 'RoomList',
    component: () => import('@/pages/rooms/index.vue')
  },
  {
    path: '/rooms/create',
    name: 'RoomCreate',
    component: () => import('@/pages/rooms/Create.vue')
  },
  {
    path: '/rooms/:id',
    name: 'RoomDetail',
    component: () => import('@/pages/rooms/Detail.vue')
  },
  {
    path: '/rooms/:id/edit',
    name: 'RoomEdit',
    component: () => import('@/pages/rooms/Edit.vue')
  },
  {
    path: '/reservations',
    name: 'ReservationList',
    component: () => import('@/pages/reservations/index.vue')
  },
  {
    path: '/reservations/create',
    name: 'ReservationCreate',
    component: () => import('@/pages/reservations/Create.vue')
  },
  {
    path: '/reservations/:id',
    name: 'ReservationDetail',
    component: () => import('@/pages/reservations/Detail.vue')
  },
  {
    path: '/users',
    name: 'UserList',
    component: () => import('@/pages/users/index.vue')
  },
  // ê´€ë¦¬ì í˜ì´ì§€ë“¤
  {
    path: '/admin',
    name: 'AdminDashboard',
    component: () => import('@/pages/admin/index.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/pages/auth/Login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/pages/auth/Register.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('@/pages/NotFound.vue')
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { top: 0 }
    }
  }
})

// ë„¤ë¹„ê²Œì´ì…˜ ê°€ë“œ
router.beforeEach((to, from, next) => {
  // ì¸ì¦ì´ í•„ìš”í•œ í˜ì´ì§€ ì²´í¬
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  const requiresAdmin = to.matched.some(record => record.meta.requiresAdmin)
  const isAuthenticated = localStorage.getItem('token') // ì‹¤ì œë¡œëŠ” ë” ë³µì¡í•œ ì¸ì¦ ë¡œì§ í•„ìš”
  const isAdmin = localStorage.getItem('userRole') === 'ADMIN' // ì‹¤ì œë¡œëŠ” ë” ë³µì¡í•œ ê¶Œí•œ ì²´í¬ í•„ìš”
  
  if (requiresAuth && !isAuthenticated) {
    next('/login')
  } else if (requiresAdmin && !isAdmin) {
    next('/') // ê´€ë¦¬ìê°€ ì•„ë‹Œ ê²½ìš° í™ˆìœ¼ë¡œ ë¦¬ë‹¤ì´ë ‰íŠ¸
  } else {
    next()
  }
})

export default router 