import axios from 'axios'

/**
 * API í´ë¼ì´ì–¸íŠ¸ ì¸ìŠ¤í„´ìŠ¤
 * @type {import('axios').AxiosInstance}
 */
const apiClient = axios.create({
  baseURL: '/api',
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json'
  }
})

// ìš”ì²­ ì¸í„°ì…‰í„° - ì‚¬ìš©ì ID í—¤ë” ì¶”ê°€
apiClient.interceptors.request.use(
  /**
   * ìš”ì²­ ì„¤ì • ìˆ˜ì •
   * @param {import('axios').AxiosRequestConfig} config - axios ìš”ì²­ ì„¤ì •
   * @returns {import('axios').AxiosRequestConfig} ìˆ˜ì •ëœ ìš”ì²­ ì„¤ì •
   */
  (config) => {
    const userId = localStorage.getItem('userId')
    if (userId) {
      config.headers['X-User-Id'] = userId
    }
    return config
  },
  /**
   * ìš”ì²­ ì˜¤ë¥˜ ì²˜ë¦¬
   * @param {Error} error - ìš”ì²­ ì˜¤ë¥˜
   * @returns {Promise<never>} ê±°ë¶€ëœ Promise
   */
  (error) => {
    console.error('Request interceptor error:', error)
    return Promise.reject(error)
  }
)

// ì‘ë‹µ ì¸í„°ì…‰í„° - ì˜¤ë¥˜ ì²˜ë¦¬
apiClient.interceptors.response.use(
  /**
   * ì„±ê³µ ì‘ë‹µ ì²˜ë¦¬
   * @param {import('axios').AxiosResponse} response - axios ì‘ë‹µ ê°ì²´
   * @returns {import('axios').AxiosResponse} ì‘ë‹µ ê°ì²´
   */
  (response) => response,
  /**
   * ì‘ë‹µ ì˜¤ë¥˜ ì²˜ë¦¬
   * @param {import('axios').AxiosError} error - axios ì˜¤ë¥˜ ê°ì²´
   * @returns {Promise<never>} ê±°ë¶€ëœ Promise
   */
  (error) => {
    if (error.response) {
      // ì„œë²„ ì‘ë‹µì´ ìˆëŠ” ê²½ìš°
      console.error('API Error:', {
        status: error.response.status,
        data: error.response.data,
        url: error.config?.url
      })
    } else if (error.request) {
      // ìš”ì²­ì€ í–ˆì§€ë§Œ ì‘ë‹µì´ ì—†ëŠ” ê²½ìš°
      console.error('Network Error:', error.message)
    } else {
      // ìš”ì²­ ì„¤ì • ì¤‘ ì˜¤ë¥˜
      console.error('Request Setup Error:', error.message)
    }
    
    return Promise.reject(error)
  }
)

export default apiClient 